.side_panel {
    
    font-family: "Jost";
    display: flex;
    flex-direction: column;
    border: 2px solid grey;
    background-color: rgba(255, 255, 255, 0.25);
    grid-area: 2/1/3/2;
    width: 80vw;
    max-width: 600px;
    height: 400px;
    justify-self: center;
    align-self: center;
    position: absolute;
    top: 280px;
    left: 50%;
    transform: translate(-50%, -50%);
}

.mobile_panel {
    margin-top: 10vh;
    font-family: "Jost";
    display: flex;
    flex-direction: column;
    border: 2px solid grey;
    background-color: rgba(255, 255, 255, 0.25);
    width: 70vw;
    min-width: 320px;
    height: 650px;
    
   
  /*   transform: translate(-50%, -50%); */
}

.options_text {
    padding: 2rem;
    font-size: 1.4rem;
    text-align:center;
}

.options_display {
    padding: 2rem;
    font-size: 2rem;
    place-self: center;
    display: flex; 
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.options_input {
    width: 150px;
    height: 30px;
    padding: 10px;
    font-family: "NotoSans";
    font-size: 1.5rem;
    border: 2px solid grey;
    background-color: rgba(255, 255, 255, 0.45);
    
}

.options_button {
    width: 100px;
    height: 54px;
    font-size: 1rem;
    border: 2px solid grey;
    background-color: rgba(255, 255, 255, 0.45);
    font-family: "NotoSans";
}
