.side_panel {
    margin-top: 78px;
    font-family: "Jost";
    display: flex;
    flex-direction: column;
    border: 2px solid grey;
    background-color: rgba(255, 255, 255, 0.25);
    
    width: 500px;
    height: 570px;
    position: absolute;
    left: 80%;
    top: 1px;
   

}


.scroll_info {
    padding-top: 50px;
    width: 380px;
    font-size: 1.45rem;
    place-self: center;
    text-align: center;
}

.score_text {
    padding: 10px;
    font-size: 1.5rem;
    text-align: center;
}

.score_display {
    padding: 2rem;
    font-size: 2rem;
    place-self: center;
}
.score_share_container {
    display: flex;
    flex-direction: row;
    width: 50%;
    place-self: center;
    justify-content: space-around;
}
.score_share {

    height: 2rem;
    width: 2rem;
}

.quote_text {
    padding: 10px;
    font-size: 1.25rem;
    text-align: justify;
    
}

.bottom_image{
    width: 100%;
    height: 191px;
}