.info_panel {
    width: 60vw;
    height: 140px;
    background-color: rgba(255, 255, 255, 0.55);
    font-family: "Jost";

    display: flex;
    align-items: center;
    border: 2px solid grey;
    max-width: 700px;

    position: absolute;
    top: 580px;
    left: 50%;
    transform: translate(-50%, -50%);

}
.info_panel_translated {
    width: 60vw;
    height: 140px;
    background-color: rgba(255, 255, 255, 0.55);
    font-family: "Jost";

    display: flex;
    align-items: center;
    border: 2px solid grey;
    max-width: 700px;

    position: absolute;
    top: 580px;
    left: 10%;
    transform: translate(-50%, -50%);
    transition: left 0.5s ease-in-out;

}

.info_panel_translated_smaller {
    width: 60vw;
    height: 140px;
    background-color: rgba(255, 255, 255, 0.55);
    font-family: "Jost";

    display: flex;
    align-items: center;
    border: 2px solid grey;
    max-width: 700px;

    position: absolute;
    top: 580px;
    left: -10%;
    transform: translate(-50%, -50%);
    transition: left 0.5s ease-in-out;

}

.info_text {
    padding: 1vh;
    text-align: justify;
    font-size: 1.25rem;
}