.container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 10vh 70vh 20vh;
    width: 100vw;
    height:100vh;
    position: relative;
    background: linear-gradient(to bottom,  sandybrown, cyan);
}

._center {
   
    grid-area: 2/2/3/3;
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
}

.container_mobile {
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 100vh;
    position: relative;
    background: linear-gradient(to bottom,  sandybrown, cyan);
}

._center_mobile {
   
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
}